import header2 from '@/components/header/header2';
import payChannelComponent from '@/components/paychannel/payChannelComponent';
export default {
    data() {
        return {
            paychannelList: []
        };
    },
    computed: {
    },
    watch: {

    },
    methods: {
        getSignBank() {
            this.axios({
                method: 'post',
                url: '/Api/GuiPayByqiao',
                data: '{"id":19,"jsonrpc":"2.0","method":"hasSign","params":{"userCode":"' + this.$store.state.userCode + '"}}',
                contentType: 'application/json',
                dataType: 'json'
            }).then((result) => {
                if (result.data.result.code === 50000) {
                    this.paychannelList = result.data.result.data;
                    this.$store.state.cardInfo = this.paychannelList[0];
                    for (var i = 0; i < this.paychannelList.length; i++) {
                        switch (this.paychannelList[i].sign) {
                        case '1':
                            this.paychannelList[i].signText = '已签约';
                            break;
                        case '2':
                            this.paychannelList[i].signText = '已解约';
                            break;
                        case '3':
                            this.paychannelList[i].signText = '未签约';
                            break;
                        case '4':
                            this.paychannelList[i].signText = '解约申请中';
                            break;
                        default:
                            this.$vux.toast.text('获取签约银行异常，联系管理员', 'bottom');
                        }
                    }
                } else {
                    this.$vux.toast.text('请检查登录情况', 'bottom');
                }
            });
        }
    },
    mounted() {

    },
    created() {
        this.getSignBank();
    },
    components: {
        header2,
        payChannelComponent
    }
};



