import {mapState} from 'vuex';
export default{
    props: ['index'],
    data() {
        return {

        };
    },
    computed: {
        ...mapState(['global'])
    },
    watch: {

    },
    methods: {
        toPayChannelDetails() {
            if (this.index.sign === '1' || this.index.sign === '4') {
                this.$router.push({path: `/cardInfo/${this.index.SgnNo}`});
            } else {
                this.$router.push({path: `/signingAuthorization`});
            }
        }
    },
    created () {
    }
};



